import styles from './Footer.module.css'
import { useNavigate } from 'react-router-dom';
import { useSetStatus } from '../store/store'

export function Footer({ variation = 'main' }) {
    const navigate = useNavigate();
    const setStatus = useSetStatus();
    return (
        <div className={styles['footer__container']}>
            <a href="#header"><h2 className={styles['footer__container-title']}>JULIIAZHIVAEVA</h2></a>
            {variation === "main" ? (
            <nav className={styles['footer__container-nav']}>
                <h3 className={styles['footer__container-navTitle']}></h3>
                <a href='#training' className={styles['footer__container-navBtn']}></a>
                <a href='#diet' className={styles['footer__container-navBtn']}></a>
                <a href='#questions' className={styles['footer__container-navBtn']}>-</a>
            </nav>
            ) : ""}
            <div id='contacts' className={`${styles['footer__container-contacts']} ${variation === "main" ? "" : styles['variation-modal']}`}>
                <h3 className={styles['footer__container-contactsTitle']}></h3>
                <div className={styles['footer__container-contactsIcons']}>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/telegram.svg" alt="" /></a>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/whatsapp.svg" alt="" /></a>
                    <a href='/' className={styles['footer__container-contactsBtn']}><img className={styles['footer__container-contactsIcon']} src="/icons/instagram.svg" alt="" /></a>
                </div>
            </div>
            {variation === "main" ? (
                <button className={styles['footer__container-policy']}> </button>
            ) : (
            <div className={styles['footer__container-variation']}>
                <button onClick={() => {localStorage.removeItem("token"); navigate('/'); window.scrollTo(0, 0); setStatus('guest')}} className={styles['footer__container-variation-exit']}>  </button>
                <button className={styles['footer__container-policy']}> </button>
            </div>
            )}

        </div>
    )
}