import styles from './Loader.module.css'

export const Loader = () => (
  <div className={styles.loaderOverlay}>
    <div className={styles.spinner}>
        <svg
        className="spinner"
        width="80"
        height="80"
        viewBox="0 0 100 100"
        >
        <circle
            className="path"
            cx="25"
            cy="25"
            r="20"
            fill="none"
            strokeWidth="4"
        />
        </svg>
    </div>
  </div>
)