import styles from './Purpose.module.css'
import { useInView } from 'react-intersection-observer';

export function Purpose () {
    const { ref, inView } = useInView({
      threshold: 0.4,
      triggerOnce: true,
    });

    return (
        <div ref={ref} className={`${styles['purpose__container']} ${inView ? styles.visible : ""}`}>
            <p className={styles['purpose__container-p1']}>  - ...</p>
            <h2 className={styles['purpose__container-h']}>
                    , <br />
                 <span></span> , <span></span>  <br />
                 <span></span>  , <br />
                    
            </h2>
            <p className={styles['purpose__container-p2']}>  -    , <br />  </p>
        </div>
    )
}