import styles from './Training.module.css'
import { useInView } from 'react-intersection-observer';
import { useState } from 'react';
export function Training() {
    const { ref, inView } = useInView({
      threshold: 0.4,
      triggerOnce: true,
    });

    // 
    const [showAlert, setShowAlert] = useState(false);
    // ,   
    const handleBuyClick = () => {
    //       
    if (showAlert) return;

    setShowAlert(true);
    setTimeout(() => setShowAlert(false), 2500); //     
    };
    // 


    return ( 
        <div id='training' className={styles['training__container']}>
            <h2 ref={ref} className={`${styles['training__container-title']} ${inView ? styles.visible : ""}`}> </h2>
            <p ref={ref} className={`${styles['training__container-paragraph']} ${inView ? styles.visible : ""}`}>,      </p>
            <div className={styles['training__container-content']}>
                <div className={styles['training__container-item']}>
                    <img src="/images/training_1.png" alt="" />
                    <div className={styles['training__container-subitem']}>
                        <div className={styles['training__container-text']}>
                            <h3 className={styles['training__container-sub-title']}>  - <br /> </h3>
                            <p className={styles['training__container-sub-paragraph']}>,  ,     </p>
                            <p className={styles['training__container-sub-paragraph']}>   </p>
                            <p className={styles['training__container-sub-paragraph']}>    -     ,   </p>
                        </div>
                        <div className={styles['training__container-buy-container']}>
                            <span className={styles['training__container-price']}>1 590 .</span>
                            <button onClick={handleBuyClick} className={styles['training__container-buy-btn']}>
                                  
                                <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M20.0503 1.11441C20.0503 0.59743 19.5757 0.178338 18.9902 0.178338L9.44946 0.178339C8.86399 0.178339 8.38938 0.597431 8.38938 1.11441C8.38938 1.63138 8.864 2.05047 9.44946 2.05047L17.9301 2.05047L17.9301 9.53901C17.9301 10.056 18.4047 10.4751 18.9902 10.4751C19.5757 10.4751 20.0503 10.056 20.0503 9.53901L20.0503 1.11441ZM1 17L1.74959 17.6619L19.7398 1.77631L18.9902 1.11441L18.2406 0.452506L0.250408 16.3381L1 17Z" fill="currentColor"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
                <div className={styles['training__container-item']}>
                    <img src="/images/training_2.png" alt="" />
                    <div className={styles['training__container-subitem']}>
                        <div className={styles['training__container-text']}>
                            <h3 className={styles['training__container-sub-title']}>  - <br /> </h3>
                            <p className={styles['training__container-sub-paragraph']}>         </p>
                            <p className={styles['training__container-sub-paragraph']}>:  3-4 ., -, </p>
                        </div>
                        <div className={styles['training__container-buy-container']}>
                            <span className={styles['training__container-price']}>1 590 .</span>
                            <button onClick={handleBuyClick} className={styles['training__container-buy-btn']}>
                                  
                                <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M20.0503 1.11441C20.0503 0.59743 19.5757 0.178338 18.9902 0.178338L9.44946 0.178339C8.86399 0.178339 8.38938 0.597431 8.38938 1.11441C8.38938 1.63138 8.864 2.05047 9.44946 2.05047L17.9301 2.05047L17.9301 9.53901C17.9301 10.056 18.4047 10.4751 18.9902 10.4751C19.5757 10.4751 20.0503 10.056 20.0503 9.53901L20.0503 1.11441ZM1 17L1.74959 17.6619L19.7398 1.77631L18.9902 1.11441L18.2406 0.452506L0.250408 16.3381L1 17Z" fill="currentColor"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
                <div className={styles['training__container-item']}>
                    <img src="/images/training_3.png" alt="" />
                    <div className={styles['training__container-subitem']}>
                        <div className={styles['training__container-text']}>
                            <h3 className={styles['training__container-sub-title']}>   <br />full body</h3>
                            <p className={styles['training__container-sub-paragraph']}>     .     </p>
                            <p className={styles['training__container-sub-paragraph']}>             </p>
                        </div>
                        <div className={styles['training__container-buy-container']}>
                            <span className={styles['training__container-price']}>1 590 .</span>
                            <button onClick={handleBuyClick} className={styles['training__container-buy-btn']}>
                                  
                                <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M20.0503 1.11441C20.0503 0.59743 19.5757 0.178338 18.9902 0.178338L9.44946 0.178339C8.86399 0.178339 8.38938 0.597431 8.38938 1.11441C8.38938 1.63138 8.864 2.05047 9.44946 2.05047L17.9301 2.05047L17.9301 9.53901C17.9301 10.056 18.4047 10.4751 18.9902 10.4751C19.5757 10.4751 20.0503 10.056 20.0503 9.53901L20.0503 1.11441ZM1 17L1.74959 17.6619L19.7398 1.77631L18.9902 1.11441L18.2406 0.452506L0.250408 16.3381L1 17Z" fill="currentColor"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
                <div className={styles['training__container-item']}>
                    <img src="/images/training_4.png" alt="" />
                    <div className={styles['training__container-subitem']}>
                        <div className={styles['training__container-text']}>
                            <h3 className={styles['training__container-sub-title']}> <br /> 3-d </h3>
                            <p className={styles['training__container-sub-paragraph']}>     </p>
                            <p className={styles['training__container-sub-paragraph']}>  ,     .     </p>
                        </div>
                        <div className={styles['training__container-buy-container']}>
                            <span className={styles['training__container-price']}>1 590 .</span>
                            <button onClick={handleBuyClick} className={styles['training__container-buy-btn']}>
                                  
                                <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M20.0503 1.11441C20.0503 0.59743 19.5757 0.178338 18.9902 0.178338L9.44946 0.178339C8.86399 0.178339 8.38938 0.597431 8.38938 1.11441C8.38938 1.63138 8.864 2.05047 9.44946 2.05047L17.9301 2.05047L17.9301 9.53901C17.9301 10.056 18.4047 10.4751 18.9902 10.4751C19.5757 10.4751 20.0503 10.056 20.0503 9.53901L20.0503 1.11441ZM1 17L1.74959 17.6619L19.7398 1.77631L18.9902 1.11441L18.2406 0.452506L0.250408 16.3381L1 17Z" fill="currentColor"/>
                                </svg>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            <p className={styles['training__container-limited']}>    30 </p>

            {/*  */}
            <span
            className={`${styles.alert} ${showAlert ? styles['alert-active'] : ''}`}
            >
              <br />  
            </span>
        </div>
    )
}