import * as api from '../api/api'
import { useSetStatus } from '../store/store'
export function useAuth() {

const setStatus = useSetStatus();

const register = async (registerDataFromModal) => {
    const { registerEmail, registerPassword } = registerDataFromModal;

    const payload = {
        email: registerEmail,
        password: registerPassword
    };
    const data = await api.fetchApi('/registerUser', 'POST', payload);
    return data;
}

const auth = async (authDataFromModal) => {
    const { loginEmail , loginPassword  } = authDataFromModal;

    const payload = {
        email: loginEmail ,
        password: loginPassword
    };
    const data = await api.fetchApi('/authUser', 'POST', payload);
    if (data.ok) {
        localStorage.setItem('token',data.data.jwt_token)
        setStatus("auth")
    }
    return data;
}

const recover = async (email) => {
    const payload = {email: email};
    const data = await api.fetchApi('/fogotPassword','POST',payload);
    return data;
}

const sendRecover = async (recoverData) => {
    const {token, password} = recoverData;
    const payload = {new_pass:password, token};
    const data = await api.fetchApi('/resetPassword','POST', payload);
    return data;
}
return { register, auth, recover, sendRecover }
}